import React from "react"
import { Link } from "gatsby"
import { graphql } from "gatsby"
import Container from '@material-ui/core/Container'
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'

import Layout from "../components/layout"
import SEO from "../components/seo"
import Image from "../images/error-404.svg"

const NotFoundPage = ({ data }) => {
  return (
    <Layout>
      <SEO title="404: Not found" />
      <Container maxWidth="md" style={{ paddingTop: '60px' }}>
        <Grid container style={{ marginTop: '30px' }}>
          <Grid item xs={12} md={6} style={{ textAlign: 'center' }}>
            <img src={Image} style={{ width: '75%' }} />
          </Grid>
          <Grid item xs={12} md={6}>
            <div>
              <h1>Sorry,</h1>
              <h1>Page Not Found!</h1>
              <Link to="/" style={{ textDecoration: 'none', }}>
                <Button variant="contained" color="primary" size="large">Go To Home</Button>
              </Link>
            </div>
          </Grid>
        </Grid>
      </Container>
    </Layout>
  )
}

export const query = graphql`
  query {
    fileName: file(relativePath: { eq: "error-404.png" }) {
      childImageSharp {
        fluid(maxWidth: 2160) {
          ...GatsbyImageSharpFluid_withWebp_tracedSVG
        }
      }
    }
  }
`

export default NotFoundPage
